import React, { useEffect, useState } from "react";
import "./contribution.styles.scss";

export const Contribution = (props) => {
  const [isMobile, setIsMobile] = useState(window.innerWidth <= 768); // Adjust the width as needed

  useEffect(() => {
    const handleResize = () => {
      setIsMobile(window.innerWidth <= 768); // Update state based on window size
    };

    window.addEventListener("resize", handleResize); // Add resize event listener

    // Cleanup function to remove the event listener on unmount
    return () => {
      window.removeEventListener("resize", handleResize); 
    };
  }, []); 

  const checkside = props.styledDescription.id % 2 === 0;

  return (
    <div className="contribution">
      <div className="blocks">
        {isMobile ? (
          <>
            <h2>{props.styledDescription.title}</h2>
            <img src={props.styledDescription.imageUrl} alt="why" />
            <div
              dangerouslySetInnerHTML={{
                __html: props.styledDescription.description,
              }}
            />
          </>
        ) : (
          <>
            {checkside ? (
              <>
                <img src={props.styledDescription.imageUrl} alt="why" />
                <div className="para">
                  <h2>{props.styledDescription.title}</h2>
                  <div
                    dangerouslySetInnerHTML={{
                      __html: props.styledDescription.description,
                    }}
                  />
                </div>
              </>
            ) : (
              <>
                <div className="para">
                  <h2>{props.styledDescription.title}</h2>
                  <div
                    dangerouslySetInnerHTML={{
                      __html: props.styledDescription.description,
                    }}
                  />
                </div>
                <img src={props.styledDescription.imageUrl} alt="why" />
              </>
            )}
          </>
        )}
      </div>
    </div>
  );
};
